html {
  scroll-behavior: smooth;
}

#covidinfocontainer {
  display: flex;
  justify-content: center;
  margin-top: 50px;
  background-color: #F7D147;
  padding: 20px;
  border-radius: 20px;
}

#covidinfoparent {
  height: 600px;
}

#statsh1 {
  text-align: center;
  margin-bottom: 20px;
}

#covidinfoKosovoContainer {
  height: 400px;
}

.footer {
  background-color: #05A6A6;
  height: 200px;
}

.footername {
  width: 20rem;
  margin-top: 60px;
  color: #fff;
}

#socialfooter {
  margin-top: 45px;
}

#socialfooter > a {
  color: #fff;
}

#socialfooter > a:hover {
  text-decoration: none;
}

#footersmall {
  color: #F7D147;
}

#footersources > a {
  color: #fff;
  font-weight: 500;
}

#burimetText {
  color: #fff
}

#footersources {
  margin-top: 35px;
}

#footersources > a:hover {
  text-decoration: none;
}

#midfooter {
  text-align: center;
}

#chartsDiv > canvas {
  margin-bottom: 100px;
}


@media only screen and (max-width: 992px) {
  #covidinfoparent {
      height: 800px;
  }
}

@media only screen and (max-width: 970px) {
  .footer {
      height: 250px;
  }
}

@media only screen and (max-width: 767px) {
  .footer {
      height: 400px;
  }
}

@media only screen and (max-width: 520px) {

  #covidinfoparent{
    height: 1000px;
  }


}

@media only screen and (max-width: 575px) {

  .card-deck {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  #covidinfoKosovoContainer {
    height: 600px;
  }

}

@media only screen and (max-width: 421px) {

  #covidinfoparent {
    height: 1100px;
  }

}

@media only screen and (max-width: 409px) {

  #covidinfoKosovoContainer {
    height: 800px;
  }

}

@media only screen and (max-width: 400px) {

  #covidinfoparent{
    height: 1200px;
  }
/* 
  #covidinfoKosovoContainer {
    height: 800px;
  } */

}

@media only screen and (max-width: 336px) {

  #covidinfoparent{
    height: 1500px;
  }

}

@media only screen and (max-width: 264px) {

  #covidinfoparent{
    height: 2000px;
  }

}

@media only screen and (max-width: 216px) {

  #covidinfoparent{
    height: 2200px;
  }

}

